<template>
  <div class="folha-rosto">
    <!-- Formato EXATO conforme INSTRUO PROCESSUAL (1).pdf -->
    <div class="folha-rosto-simples">
      <div class="caixa-bordered">
        
        <div class="numero-processo">
          <strong>PROCESSO ADMINISTRATIVO N {{ numeroProcesso || '[no definido]' }}</strong>
        </div>
        
        <div class="campo">
          <strong>NOME DO RGO:</strong> {{ dadosOrgao.nome }}
        </div>
        
        <div class="campo">
          <strong>INTERESSADO(A):</strong> {{ dadosOrgao.departamento || dadosOrgao.unidade_interessada }}
        </div>
        
        <div class="campo">
          <strong>DATA DE AUTUAO:</strong> {{ dataInicio }}
        </div>
        
        <div class="campo objeto">
          <strong>OBJETO:</strong> {{ objetoTexto }}
        </div>
        
      </div>
      
      <!-- Nmero da folha -->
      <div class="folha-numero">
        <p>Fl. 001</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FolhaRosto',
  props: {
    numeroProcesso: {
      type: String,
      default: ''
    },
    tipoProcesso: {
      type: String,
      required: true,
      validator: value => ['padronizacao', 'despadronizacao'].includes(value)
    },
    dadosOrgao: {
      type: Object,
      default: () => ({
        nome: '',
        departamento: '',
        unidade_interessada: ''
      })
    },
    dataInicio: {
      type: String,
      default: () => new Date().toLocaleDateString('pt-BR')
    }
  },
  
  computed: {
    objetoTexto() {
      if (this.tipoProcesso === 'padronizacao') {
        return `CHAMAMENTO PBLICO DESTINADO  REALIZAO DO PROCEDIMENTO AUXILIAR DE PR-QUALIFICAO DE BENS PREVISTO NO ART. 80, INCISO II, DA LEI FEDERAL N 14.133/2021, OBJETIVANDO PROMOVER A SELEO TCNICA DE MARCAS E MODELOS DE PRODUTOS QUE POSSUAM OS PADRES MNIMOS DE QUALIDADE, ESTTICA, RENDIMENTO, DURABILIDADE, ADEQUAO AO USO E  FINALIDADE A QUE SE DESTINAM, CONFORME AS CARACTERSTICAS E CONDIES CONSTANTES NO EDITAL E SEUS ANEXOS, PARA SEREM INCLUDOS NO CATLOGO ELETRNICO DE BENS PADRONIZADOS, COM VISTAS S AQUISIES EVENTUAIS E FUTURAS.`
      } else {
        return `DESPADRONIZAO DE MARCA(S) E MODELO(S) DE PRODUTO(S) QUE NO MAIS ATENDE(M) AOS PADRES MNIMOS DE QUALIDADE, ESTTICA, RENDIMENTO, DURABILIDADE E ADEQUAO AO USO E  FINALIDADE A QUE SE DESTINA(M), COM VISTAS  SUA RETIRADA DO CATLOGO ELETRNICO DE BENS PADRONIZADOS.`
      }
    }
  }
}
</script>

<style scoped>
.folha-rosto {
  width: 21cm;
  min-height: 29.7cm;
  margin: 0 auto;
  padding: 2cm;
  background: white;
  font-family: 'Times New Roman', serif;
  font-size: 12pt;
  line-height: 1.6;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.folha-rosto-simples {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 25cm;
}

.caixa-bordered {
  border: 2px solid #000;
  padding: 1.5cm;
  max-width: 16cm;
  width: 100%;
}

.numero-processo {
  font-weight: bold;
  font-size: 14pt;
  margin-bottom: 1cm;
  text-align: center;
}

.campo {
  margin-bottom: 1cm;
  text-align: left;
}

.campo.objeto {
  text-align: justify;
  line-height: 1.4;
}

.campo strong {
  font-weight: bold;
}

.folha-numero {
  margin-top: 4cm;
  text-align: center;
  font-size: 10pt;
  color: #666;
}

.folha-numero p {
  margin: 0;
}

/* Estilos para impresso */
@media print {
  .folha-rosto {
    box-shadow: none;
    margin: 0;
    padding: 1.5cm;
  }
}

/* Responsividade para telas menores */
@media (max-width: 21cm) {
  .folha-rosto {
    width: 100%;
    margin: 1cm;
    padding: 1cm;
  }
  
  .caixa-bordered {
    max-width: 100%;
    padding: 1cm;
  }
}
</style>