  // =====================================================
  // GESTO DE DFD (DOCUMENTO DE FORMALIZAO DE DEMANDA)
  // =====================================================

  static async criarDFD(processoId, dadosDFD) {
    try {
      const tenantId = await getTenantId()
      const { data: sessionData } = await supabase.auth.getSession()
      const user = sessionData?.session?.user

      if (!tenantId || !user) {
        throw new Error('Usurio no autenticado ou sem tenant')
      }

      // Gerar HTML do DFD
      const htmlDFD = this.gerarHTMLDFD(dadosDFD)

      // Registrar documento com numerao sequencial automtica
      const documento = await NumeracaoDocumentosService.registrarDocumento({
        processo_id: processoId,
        tenant_id: tenantId,
        tipo_documento: 'DFD',
        nome_documento: `Documento de Formalizao de Demanda - ${dadosDFD.modelo_usado.toUpperCase()}`,
        conteudo: dadosDFD,
        conteudo_html: htmlDFD,
        criado_por: user.id
      })

      // Criar registro especfico do DFD (se necessrio manter tabela separada)
      const dfd = {
        id: documento.id, // Usar mesmo ID do documento
        processo_id: processoId,
        documento_id: documento.id,
        ...dadosDFD,
        tenant_id: tenantId,
        folha_numero: documento.folha_numero
      }

      const { data: dfdData, error: dfdError } = await supabase
        .from('dfd_processo')
        .insert(dfd)
        .select()
        .single()

      if (dfdError) {
        console.error('Erro ao criar registro DFD:', dfdError)
        // Se falhar, remover documento criado
        await NumeracaoDocumentosService.removerDocumento(documento.id)
        throw dfdError
      }

      console.log(`DFD criado com sucesso: ${documento.folha_numero}`)

      return { 
        dfd: dfdData, 
        documento: documento,
        folha_numero: documento.folha_numero
      }
    } catch (error) {
      console.error('Erro no servio de DFD:', error)
      throw error
    }
  }

